'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { auth, db } from '@/app/config/firebase';
import {
  collection,
  query,
  where,
  orderBy,
  getDocs,
  doc,
  updateDoc,
  Timestamp
} from 'firebase/firestore';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Calendar } from "@/components/ui/calendar";
import { format } from "date-fns";
import { Calendar as CalendarIcon, Clock, Edit, LogOut } from "lucide-react";
import type { Appointment } from '@/app/types/appointment';

export default function DoctorDashboard() {
  const [appointments, setAppointments] = useState<Appointment[]>([]);
  const [selectedAppointment, setSelectedAppointment] = useState<Appointment | null>(null);
  const [isEditing, setIsEditing] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [filter, setFilter] = useState<'all' | 'pending' | 'confirmed' | 'completed' | 'cancelled'>('all');
  const router = useRouter();

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (!user) {
        router.push('/doctor/login');
      } else {
        fetchAppointments(user.uid);
      }
    });

    return () => unsubscribe();
  }, [router]);

  const fetchAppointments = async (doctorId: string) => {
    try {
      const appointmentsRef = collection(db, 'appointments');
      const q = query(
        appointmentsRef,
        where('doctorId', '==', doctorId),
        orderBy('date', 'desc')
      );

      const querySnapshot = await getDocs(q);
      const appointmentsList: Appointment[] = [];

      querySnapshot.forEach((doc) => {
        const data = doc.data();
        appointmentsList.push({
          id: doc.id,
          ...data,
          date: data.date.toDate(),
          createdAt: data.createdAt.toDate(),
          updatedAt: data.updatedAt.toDate(),
        } as Appointment);
      });

      setAppointments(appointmentsList);
    } catch (error) {
      console.error('Error fetching appointments:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleUpdateAppointment = async (appointmentId: string, updates: Partial<Appointment>) => {
    try {
      const appointmentRef = doc(db, 'appointments', appointmentId);
      await updateDoc(appointmentRef, {
        ...updates,
        updatedAt: Timestamp.now()
      });

      // Refresh appointments
      if (auth.currentUser) {
        fetchAppointments(auth.currentUser.uid);
      }

      setIsEditing(false);
      setSelectedAppointment(null);
    } catch (error) {
      console.error('Error updating appointment:', error);
    }
  };

  const handleLogout = async () => {
    try {
      await auth.signOut();
      router.push('/doctor/login');
    } catch (error) {
      console.error('Error logging out:', error);
    }
  };

  const filteredAppointments = appointments.filter(appointment => {
    if (filter === 'all') return true;
    return appointment.status === filter;
  });

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Navigation Bar */}
      <nav className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16">
            <div className="flex items-center">
              <h1 className="text-xl font-semibold text-gray-900">Doctor Dashboard</h1>
            </div>
            <div className="flex items-center space-x-4">
              <Button variant="ghost" onClick={handleLogout}>
                <LogOut className="h-5 w-5 mr-2" />
                Logout
              </Button>
            </div>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
        {/* Filter Section */}
        <div className="mb-6">
          <Select
            value={filter}
            onValueChange={(value: any) => setFilter(value)}
          >
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Filter appointments" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Appointments</SelectItem>
              <SelectItem value="pending">Pending</SelectItem>
              <SelectItem value="confirmed">Confirmed</SelectItem>
              <SelectItem value="completed">Completed</SelectItem>
              <SelectItem value="cancelled">Cancelled</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Appointments Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {isLoading ? (
            <p>Loading appointments...</p>
          ) : filteredAppointments.length === 0 ? (
            <p>No appointments found.</p>
          ) : (
            filteredAppointments.map((appointment) => (
              <Card key={appointment.id} className="hover:shadow-md transition-shadow">
                <CardHeader>
                  <CardTitle className="flex justify-between items-center">
                    <span>{appointment.patientName}</span>
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => setSelectedAppointment(appointment)}
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                      </DialogTrigger>
                      <DialogContent>
                        <DialogHeader>
                          <DialogTitle>Edit Appointment</DialogTitle>
                          <DialogDescription>
                            Update appointment details for {appointment.patientName}
                          </DialogDescription>
                        </DialogHeader>
                        <div className="space-y-4 py-4">
                          <div className="space-y-2">
                            <label className="text-sm font-medium">Status</label>
                            <Select
                              value={selectedAppointment?.status}
                              onValueChange={(value: any) =>
                                setSelectedAppointment(prev =>
                                  prev ? { ...prev, status: value } : null
                                )
                              }
                            >
                              <SelectTrigger>
                                <SelectValue placeholder="Select status" />
                              </SelectTrigger>
                              <SelectContent>
                                <SelectItem value="pending">Pending</SelectItem>
                                <SelectItem value="confirmed">Confirmed</SelectItem>
                                <SelectItem value="completed">Completed</SelectItem>
                                <SelectItem value="cancelled">Cancelled</SelectItem>
                              </SelectContent>
                            </Select>
                          </div>

                          <div className="space-y-2">
                            <label className="text-sm font-medium">Diagnosis</label>
                            <Textarea
                              value={selectedAppointment?.diagnosis || ''}
                              onChange={(e) =>
                                setSelectedAppointment(prev =>
                                  prev ? { ...prev, diagnosis: e.target.value } : null
                                )
                              }
                              placeholder="Enter diagnosis"
                            />
                          </div>

                          <div className="space-y-2">
                            <label className="text-sm font-medium">Prescription</label>
                            <Textarea
                              value={selectedAppointment?.prescription || ''}
                              onChange={(e) =>
                                setSelectedAppointment(prev =>
                                  prev ? { ...prev, prescription: e.target.value } : null
                                )
                              }
                              placeholder="Enter prescription"
                            />
                          </div>

                          <div className="space-y-2">
                            <label className="text-sm font-medium">Notes</label>
                            <Textarea
                              value={selectedAppointment?.notes || ''}
                              onChange={(e) =>
                                setSelectedAppointment(prev =>
                                  prev ? { ...prev, notes: e.target.value } : null
                                )
                              }
                              placeholder="Add notes"
                            />
                          </div>

                          <div className="flex justify-end space-x-2">
                            <Button
                              variant="outline"
                              onClick={() => {
                                setSelectedAppointment(null);
                                setIsEditing(false);
                              }}
                            >
                              Cancel
                            </Button>
                            <Button
                              onClick={() => {
                                if (selectedAppointment) {
                                  handleUpdateAppointment(selectedAppointment.id, {
                                    status: selectedAppointment.status,
                                    diagnosis: selectedAppointment.diagnosis,
                                    prescription: selectedAppointment.prescription,
                                    notes: selectedAppointment.notes,
                                  });
                                }
                              }}
                            >
                              Save Changes
                            </Button>
                          </div>
                        </div>
                      </DialogContent>
                    </Dialog>
                  </CardTitle>
                  <CardDescription>
                    <div className="flex items-center space-x-2">
                      <CalendarIcon className="h-4 w-4" />
                      <span>{format(appointment.date, 'PPP')}</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Clock className="h-4 w-4" />
                      <span>{appointment.time}</span>
                    </div>
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <div>
                      <span className="font-medium">Status: </span>
                      <span className={`
                        inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium
                        ${appointment.status === 'confirmed' ? 'bg-green-100 text-green-800' : ''}
                        ${appointment.status === 'pending' ? 'bg-yellow-100 text-yellow-800' : ''}
                        ${appointment.status === 'cancelled' ? 'bg-red-100 text-red-800' : ''}
                        ${appointment.status === 'completed' ? 'bg-blue-100 text-blue-800' : ''}
                      `}>
                        {appointment.status.charAt(0).toUpperCase() + appointment.status.slice(1)}
                      </span>
                    </div>
                    <div>
                      <span className="font-medium">Symptoms: </span>
                      <p className="text-sm text-gray-600">{appointment.symptoms}</p>
                    </div>
                    {appointment.diagnosis && (
                      <div>
                        <span className="font-medium">Diagnosis: </span>
                        <p className="text-sm text-gray-600">{appointment.diagnosis}</p>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))
          )}
        </div>
      </main>
    </div>
  );
}
